@use 'base/variables' as v;
@use 'base/mixins' as m;

.site-header {
    @include m.bgImage('../img/header.jpg', v.$secundarioOpacity);
    padding: 5rem 0;
    .logo {
        max-width: 20rem;
        margin: 0 auto;
        @include m.tablet {
            margin: 0;
        }
    }

    .contenido-header {
        margin-top: 10rem;
        @include m.desktop {
            margin-top: 15rem;
        }
        h1 {
            color: v.$blanco;
            font-size: 4.5rem;
            text-align: center;
            @include m.tablet {
                font-size: 6rem;
                max-width: 50%;
                text-align: left;
            }
            @include m.desktop {
                max-width: 40%;   
            }
        }
    }
}